/* src/styles.css */

body {
    margin: 2%;
    padding: 2%;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana,
      sans-serif;
  }
  
  header {
    text-align: center;
    padding: 20px;
  }
  
  .intro-image {
    width: 100%;
    max-height: 300px;
    object-fit: cover;
  }
  
  nav {
    margin-top: 10px;
  }
  
  nav a {
    margin: 0 15px;
    text-decoration: none;
    color: #333;
  }
  
  nav a:hover {
    color: #007BFF;
  }
  
  .post-section {
    text-align: center;
    padding: 20px;
    border-bottom: 1px solid #ccc;
  }
  
  .post-section h2 {
    margin-top: 0;
  }
  
  .post-section p {
    font-size: 1em;
  }
  
  .post-section a {
    text-decoration: none;
    color: #007BFF;
  }
  
  .post-section a:hover {
    text-decoration: underline;
  }
  
  article {
    padding: 20px;
  }
  
  article h1 {
    margin-top: 0;
  }
  